<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <div>
        <van-nav-bar title="" left-arrow @click-left="onClickLeft" right-text="" @click-right="submitFn" />
        <van-field v-model="oldPsw" label="" type="password" placeholder="" right-icon="close"
            @click-right-icon="oldPsw=''" />
        <van-field v-model="newPsw" label="" type="password" placeholder="" right-icon="close"
            @click-right-icon="newPsw=''" />

    </div>
</template>
<script>
import { Toast } from 'vant';
import { passwordAPI } from '@/api'
export default {
    data() {
        return {
            oldPsw: '',
            newPsw: ''
        }
    },
    methods: {
        onClickLeft() {
            this.$router.go(-1)
        },
        async submitFn() {
            // application/x-www-form-urlencoded
            const params = new URLSearchParams();
            params.append('oldPwd', this.oldPsw);
            params.append('newPwd', this.newPsw);
            const res = await passwordAPI(sessionStorage.getItem('token'), params)
            if (res.data.status != 0) {
                return Toast('')
            }
            this.$router.go(-1)
            return Toast('')
        }
    },
};
</script>
<style scoped>
.van-cell-group {
    margin: 10px 0
}
</style>