//  axios
import request from "@/utils/request"


//  - 
export const nickName = (token,nickname) => request({
    url: `/my/editNickname`,
    method:"POST",
    headers: {
        'Authorization': token
    },
    data:nickname
})

//  - 
export const birth = (token,birth) => request({
    url: `/my/editBirth`,
    method:"POST",
    headers: {
        'Authorization': token
    },
    data:birth
})

