<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <div>
        <!--  -->
        <van-search showAction v-model="value" shape="round" background="#00a0f0" placeholder="">
            <template #action>
                <van-icon name="comment-o" size='25' class="comment" color="white" />
            </template>
        </van-search>
        <!--  -->
        <van-tabs v-model="active" background="#f7f8f9">
            <van-tab title="">
                <van-list v-model="loading" :finished="finished" finished-text="" @load="onLoad">
                    <SingleOrder v-for="obj in ordersList" :key="obj.id" :order="obj"></SingleOrder>
                </van-list>
            </van-tab>
            <van-tab title="">
            </van-tab>
            <van-tab title="">
            </van-tab>
        </van-tabs>
    </div>
</template>
<script>
import SingleOrder from '@/components/SingleOrder.vue';
import { ordersAPI } from '@/api';
export default {
    data() {
        return {
            value: "",
            active: 0,
            ordersList: [],
            loading: false,
            finished: false,
        };
    },
    components: { SingleOrder },
    async created() {
        // 
        const res = await ordersAPI(sessionStorage.getItem('token'))
        // console.log(res);
        this.ordersList = res.data.data
    },
    methods: {
        onLoad() {
            // 
            // setTimeout  ajax 
            setTimeout(() => {
                // 
                this.loading = false;
                this.finished = true;
            }, 1000);
        },
    }
};
</script>
<style scoped>
.comment {
    vertical-align: middle;
}
</style>