<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <!--  -->
    <van-nav-bar left-text="" left-arrow @click-left="onClickLeft" />

    <!-- logo -->
    <van-row type="flex" justify="center" class="logo">
      <van-col span="4">
        <img src="@/assets/icon.png" alt="" width="100%">
      </van-col>
    </van-row>

    <!--  -->
    <van-form @submit="onSubmit">
      <!--  -->
      <van-field v-model="phone" name="phone" label="" placeholder=""
        :rules="[{ validator:phoneValidator, message: '11' }]" />
      <!--  -->
      <van-field v-model="password" type="password" name="password" label="" placeholder=""
        :rules="[{ validator:pswValidator, message: '8-16111' }]" />
      <!--  -->
      <van-field v-model="pswconfirm" type="password" name="pswconfirm" label="" placeholder=""
        :rules="[{ validator:pswConfirm, message: '' }]" />
      <div style="margin: 16px;">
        <van-button round hairline plain size="large" color="#00a0f0" native-type="submit"></van-button>
      </div>
    </van-form>
  </div>
</template>
<script>
import { registerAPI } from '@/api'
import { Toast } from 'vant';
export default {
  data() {
    return {
      phone: '',
      password: '',
      pswconfirm: ''
    };
  },
  methods: {
    async onSubmit(vals) {
      // console.log(vals);
      // application/x-www-form-urlencoded
      const params = new URLSearchParams();
      params.append('phone', vals.phone);
      params.append('password', vals.password);
      const res = await registerAPI(params)
      if (res.data.status === 0) {
        this.$router.push({
          path: '/login'
        })
        return Toast(',');
      } else {
        this.phone = ''
        this.password = ''
        this.pswconfirm = ''
        return Toast('');
      }
    },
    onClickLeft() {
      this.$router.push({
        path: "/layout/home"
      })
    },
    phoneValidator(val) {
      return /^1(3\d|4[5-9]|5[0-35-9]|6[2567]|7[0-8]|8\d|9[0-35-9])\d{8}$/.test(val);
    },
    pswValidator(val) {
      //8-16111
      return /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/.test(val)
    },
    pswConfirm(val) {
      return val == this.password
    }
  },
};
</script>
<style scoped>
.logo {
  width: 100%;
  margin-top: 50px;
  text-align: center;
  font-size: 26px;
  font-weight: bold;
  margin-bottom: 20px;
}

.van-button {
  margin-bottom: 10px;
}
</style>