<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <div>
        <van-nav-bar title="" left-arrow @click-left="onClickLeft" right-text="" @click-right="submitFn" />
        <van-cell-group>
            <van-cell title="" is-link arrow-direction="down" :value="date" @click="isShow = true" />
            <van-datetime-picker v-model="currentDate" type="date" title="" :min-date="minDate"
                :max-date="maxDate" v-if="isShow" @confirm='confirmFn' @cancel="isShow = false" />
        </van-cell-group>
    </div>
</template>
<script>
import { Toast } from 'vant';
import moment from 'moment'
import { birthAPI } from '@/api'
export default {
    data() {
        return {
            isShow: false,
            date: this.$route.params.birth,
            currentDate: new Date(this.$route.params.birth),
            minDate: new Date(1922, 0, 1),
            maxDate: new Date(),
        };
    },
    methods: {
        onClickLeft() {
            this.$router.go(-1)
        },
        async submitFn() {
            // application/x-www-form-urlencoded
            const params = new URLSearchParams();
            params.append('birth', this.date);
            const res = await birthAPI(sessionStorage.getItem('token'), params)
            console.log(res);
            if (res.data.status != 0) {
                return Toast('')
            }
            this.$router.go(-1)
            return Toast('')
        },
        confirmFn(value) {
            const date = moment(value).format('YYYY-MM-DD')
            // const date = moment.format(value)
            this.date = date
            this.isShow = false
        }
    },

};
</script>
<style scoped>
.van-datetime-picker {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}
</style>