<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <div class="orderDetail">
        <!--  -->
        <div class="top">
            <!-- navbar  -->
            <van-nav-bar left-arrow :border="false" @click-left="onClickLeft">
                <template #left>
                    <van-icon name="arrow-left" color="white" size="20" />
                </template>
                <template #title>
                    <p class="nav-title"></p>
                </template>
            </van-nav-bar>
            <!--  -->
            <van-tabs type="card" color="#9bdbfd">
                <!--  -->
                <van-tab title="" class="main">
                    <div class="card">
                        <!--  -->
                        <van-cell-group>
                            <!--  -->
                            <van-cell is-link :title="address" @click="addressShow = true" />
                            <van-action-sheet v-model="addressShow" title="" :actions="addressList"
                                @select="addressSelect" close-on-click-action>
                                <van-button plain type="info" size="large"></van-button>
                            </van-action-sheet>
                            <!--  -->
                            <van-cell title="" value="" />
                            <!--  -->
                            <van-cell is-link :title="payment" @click="paymentShow = true" />
                            <van-action-sheet v-model="paymentShow" :actions="paymentList" @select="paymentSelect"
                                cancel-text="" description="">
                            </van-action-sheet>
                        </van-cell-group>
                    </div>
                    <!--  -->
                    <div class="detail">
                        <!--  -->
                        <div class="foodList">
                            <p class="title">{{store.name}}</p>
                            <OrderItem v-for="(obj,index) in cart" :key="index" :productId="obj.product" :num="obj.num">
                            </OrderItem>
                        </div>
                        <!--  -->
                        <div class="fee">
                            <van-cell-group>
                                <van-cell title="" value="1" :border="false">
                                    <template #default><span></span>{{service}}</template>
                                </van-cell>
                                <van-cell title="" :border="false">
                                    <template #default><span></span>{{currency(cart.length)}}</template>
                                </van-cell>
                                <van-cell title="" :border="false">
                                    <template #default><span></span>{{currency(store.delivery)}}</template>
                                </van-cell>
                            </van-cell-group>
                        </div>
                        <!--  -->
                        <div class="total">
                            <p> {{total}}</p>
                        </div>
                        <!--  -->
                        <van-cell-group>
                            <van-cell title="" value="" :border="false" />
                            <van-cell title="" value=" >" :border="false" />
                        </van-cell-group>
                    </div>

                </van-tab>
                <!--  -->
                <van-tab title="" class="main">
                    <div class="card">
                        <!--  -->
                        <van-cell-group>
                            <!--  -->
                            <van-cell title="&">
                                <template #label>
                                    <img src="@/assets/map.jpg" alt="" width="100%">
                                </template>
                            </van-cell>
                            <!--  -->
                            <van-cell title="" value="13538991133" />
                            <!--  -->
                            <van-cell is-link :title="payment" @click="paymentShow = true" />
                            <van-action-sheet v-model="paymentShow" :actions="paymentList" @select="paymentSelect"
                                cancel-text="" description="">
                            </van-action-sheet>
                        </van-cell-group>
                    </div>
                    <OrderDetail></OrderDetail>
                </van-tab>
            </van-tabs>
            <van-submit-bar button-text="" button-color="#48c2fe" @submit="onSubmit" />
        </div>

    </div>
</template>
<script>

import { Toast } from 'vant';
import currency from 'currency.js';
import OrderItem from '@/components/OrderItem.vue';
import { storeAPI, submitOrderAPI, submitOrderDetailAPI } from '@/api';
export default {
    components: { OrderItem },
    data() {
        return {
            paymentShow: false,
            paymentList: [{ name: '' }, { name: '' }, { name: '' }, { name: '' }],
            payment: ':',
            addressShow: false,
            addressList: [{ name: ': 13538991133', subname: '' }, { name: ': 13538991133', subname: '' }],
            address: '',
            cart: JSON.parse(sessionStorage.getItem('cart')),
            storeId: this.$route.params.storeId,
            store: {}
        };
    },
    methods: {
        onClickLeft() {
            this.$router.go(-1)
        },
        paymentSelect(item) {
            // 
            //  close-on-click-action 
            this.paymentShow = false;
            Toast(item.name);
            this.payment = item.name
        },
        onCancel() {
            Toast('');
        },
        addressSelect(item) {
            // 
            //  close-on-click-action 
            this.paymentShow = false;
            Toast(item.subname);
            this.address = item.subname
        },
        async onSubmit() {
            // application/x-www-form-urlencoded
            // 
            const params = new URLSearchParams();
            params.append('storeId', this.store.id);
            params.append('total', this.total);
            params.append('address', '1');
            params.append('paymentType', '1');
            params.append('status', '1');
            params.append('service', this.service);
            params.append('pack', this.cart.length);
            params.append('postage', this.store.delivery);
            const res = await submitOrderAPI(sessionStorage.getItem('token'), params)

            // 
            const params2 = new URLSearchParams();
            params2.append('orderId', res.data.orderId)
            params2.append('productList', sessionStorage.getItem('cart'))
            await submitOrderDetailAPI(sessionStorage.getItem('token'), params2)
            this.$router.push({
                path: '/layout/order'
            })
            Toast('');
        }
    },
    async created() {
        const res = await storeAPI({ storeId: this.storeId })
        this.store = res.data.data[0]
        console.log(res);
    },
    computed: {
        productTotal() {
            let total = 0
            this.cart.forEach((v) => {
                total = total + v.price
            });
            return currency(total)
        },
        service() {
            return currency(parseFloat(this.productTotal) * 0.1)
        },
        total() {
            let total = parseFloat(this.service) + this.cart.length + this.store.delivery
            return currency(total + parseFloat(this.productTotal))
        }
    }
};
</script>
<style scoped>
.nav-title {
    color: white;
}

.van-nav-bar {
    background-color: #0aafff;
}

.top {
    background-image: linear-gradient(#0aafff, 40%, #d0ecfc);
}

.top .statu-text {
    color: white;
    margin-left: 15px;
}

.card {
    margin: 10px 10px;
    border-radius: 10px;
    overflow: hidden;
}

.top .status .row {
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid #f7f8f9;
}

.footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    left: 0;
    position: fixed;
    background-color: white;
    width: 100%;
}

.footer .left {
    display: flex;
}

.footer .left p {
    font-size: 16px;
    display: flex;
    align-items: center;
    padding-left: 20px;
}

.footer .footer-icon {
    padding-left: 10px;
}

.main {
    padding-bottom: 44px;
}




.detail {
    background-color: white;
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    box-sizing: border-box;
}

.title {
    font-size: 16px;
    font-weight: bold;
    padding: 15px 0 5px 15px;
}

.detail .fee {
    margin-top: 10px;
    border-top: 1px solid #f7f8f9;
}

.detail .fee .feeItem {
    display: flex;
    justify-content: space-between;
    padding: 0 15px;
    font-size: 14px;
}

.detail .total {
    text-align: end;
    padding-right: 15px;
    padding-bottom: 10px;
    padding-top: 10px;
    font-size: 16px;
    color: red;
}
</style>