<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <div>
        <van-nav-bar title="" left-arrow @click-left="onClickLeft" />
        <van-cell-group>
            <van-cell title="" is-link to='/settingLayout/user' />
            <van-cell title="" is-link />
        </van-cell-group>
        <van-cell-group>
            <van-cell title="" value="1.0.0" is-link />
            <van-cell title="" is-link />
        </van-cell-group>
        <van-button type="default" block @click="logoutFn"></van-button>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    methods: {
        onClickLeft() {
            this.$router.go(-1)
        },
        logoutFn() {
            sessionStorage.removeItem('token');
            sessionStorage.removeItem('islogin');
            Toast('');
            this.$router.push({
                path: '/login'
            })
        }
    },
};
</script>
<style scoped>
.van-cell-group {
    margin: 10px 0;
}
</style>