<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <div>
        <van-nav-bar title="" left-arrow @click-left="onClickLeft" right-text="" @click-right="submitFn" />
        <van-cell-group>
            <van-field v-model="value" label="" placeholder="" right-icon="close" @click-right-icon="clearFn" />
        </van-cell-group>
    </div>
</template>
<script>
import { Toast } from 'vant';
import { emailAPI } from '@/api'
export default {
    data() {
        return {
            value: this.$route.params.email,
        };
    },
    methods: {
        onClickLeft() {
            this.$router.go(-1)
        },
        clearFn() {
            this.value = ''
        },
        async submitFn() {
            // application/x-www-form-urlencoded
            const params = new URLSearchParams();
            params.append('email', this.value);
            const res = await emailAPI(sessionStorage.getItem('token'), params)
            if (res.data.status != 0) {
                return Toast('')
            }
            this.$router.go(-1)
            return Toast('')
        },
    },
};
</script>
